.homepage-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  max-width: 500px;
}

.title-section {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 25px;
  width: 100%;
}

.logo-image {
  max-width: 100px;
  max-height: 100px;
  margin-right: 20px;
}

.loading-container {
  text-align: center;
}

.loading-message {
  font-size: 20px;
  font-weight: bold;
  color: #333;
  padding: 100px 0px;
}
